import React, { useState } from 'react';


// beside (props) we can ({prop1, prop2})
const Search = (props) => {
  const {
    // заглушка, пропсов не будет vvv
    searchMovies = Function.prototype,
  } = props;

  const [search, setSearch] = useState('');
  const [type, setType] = useState('all');

  const searchEnter = (e) => {
    if (e.key === 'Enter')
      searchMovies(search, type);
  }

  const searchButton = (e) => {
    searchMovies(search, type);
  }

  const filterHandle = (e) => {
    setType(e.target.dataset.type)
    searchMovies(search, e.target.dataset.type)


  }


  return <div className="row">
    <div className="input-field col s12">
      <input
        placeholder="Search..."
        type="search"
        className="validate"
        value={search}
        onChange={(e) => setSearch(e.target.value)}
        onKeyPress={(e) => searchEnter(e)}
      />
      <button class="btn-flat search-btn"
        onClick={(e) => searchButton(e)}
      >FIND</button>
    </div>

    <label>
      <input
        name="type"
        type="radio"
        data-type="all"
        onChange={filterHandle}
        checked={type === 'all'}
      />
      <span>All Types</span>
    </label>
    <label>
      <input
        name="type"
        type="radio"
        data-type="movie"
        onChange={filterHandle}
        checked={type === 'movie'}
      />
      <span>Movies</span>
    </label>
    <label>
      <input
        name="type"
        type="radio"
        data-type="series"
        onChange={filterHandle}
        checked={type === 'series'}
      />
      <span>Series</span>
    </label>
    <label>
      <input
        name="type"
        type="radio"
        data-type="game"
        onChange={filterHandle}
        checked={type === 'game'}
      />
      <span>Games</span>
    </label>
  </div>
}


export { Search }